@use "../mixins.scss" as *;
@use "../variables.scss" as *;

.example {
  width: 100vw;
  position: relative;
  padding-top: 25px;
  padding-bottom: 40px;
  background-color: #eaeaea;

  @media (min-width: $tablet-width) {
    background-color: transparent;
  }
}

.example__container {
  @include site-container;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    background-color: transparent;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.example__title {
  @include site-title(36px, 40px);
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    @include site-title(60px, 60px);
    margin-bottom: 63px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    margin-bottom: 146px;
  }
}

.example__text {
  @include site-content(14px, 18px);
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    @include site-content(16px, 24px);
    margin-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    grid-row: 2/3;
    padding-right: 177px;
    margin-bottom: 69px;
  }
}

.example__list-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $tablet-width) {
    gap: 42px;
    margin-bottom: 255px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    grid-row: 3/4;
    padding-right: 244px;
    margin-bottom: 75px;
  }
}

.example__expenses {
  @include site-title(14px, 20px);
  text-transform: uppercase;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    @include site-title(20px, 20px);
    display: flex;
    flex-direction: column;
    gap: 9px;
    align-items: flex-end;
    margin: 0;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    gap: 63px;
    margin: 0;
    margin-top: 13px;
  }
}

.example__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  padding: 14px 22px 0;
  align-items: center;
  width: 124px;
  min-height: 56px;
  margin: 0;
  border: 1px solid  #cdcdcd;
  border-radius: 5px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: 162px;
    min-height: 77px;
    padding: 18px 8px 0;
    justify-content: center;
  }
}

.example__weight,
.example__time {
  @include site-content(12px, 12px);
  text-align: center;
  margin-bottom: -14px;
  z-index: 1;
  background-color: #eaeaea;

  @media (min-width: $tablet-width) {
    @include site-content(14px, 14px);
    margin-bottom: -5px;
    background-color: #ffffff;
    padding: 0 9px;
  }

  @media (min-width: $desktop-width) {
    background-color: $bgcolor-secondary;
  }
}

.example__description {
  @include site-title(24px, 24px);
  margin-bottom: 7px;
  text-align: center;
  vertical-align: middle;

  @media (min-width: $tablet-width) {
    @include site-title(30px, 37px);
    margin-bottom: 11px;
  }
}

.example__slider {
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $desktop-width) {
    grid-row: 1/4;
    grid-column: 2/3;
  }
}

.example__decor {
  @media (min-width: $tablet-width) {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 462px;
    background-color: $bgcolor-secondary;
    z-index: -1;
  }
}

